
.secao-banner {
    min-width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.secao-banner .imagem, .secao-banner .titulo {

    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    height:480px;
    padding: 0 5%;
}

.imagem figure {
    height:479px;
    background-image: url('../../assets/banner.png');
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 0 0 25px  0;
}

.titulo strong {
    font-size: 30px;
    font-weight: bolder;
}

.sun-titulo {
    background-color: #fafbfc;
    border-radius: 25px 0 0  0;
    color: #022144;
}

.moon-image {
    background-color: #252525;
    border-radius: 0 0 25px   0;
}

@media screen and (max-width: 768px) {
  
    .secao-banner .imagem{
      display: none;
    }
    .secao-banner .titulo {
        width: 100%;
    }
  
  }