footer {
    padding: 5% 0;
    text-align: center;
}

.sun-footer {

    background-color: #fff;
    color: #8a8fa3;
}
.moon-footer {

    background-color: #252525;
    color: #8a8fa3;
}

.rodape {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.rodape p {
    padding: 3%;
}
.rodape span {
    color: #feb633;
    font-weight: bolder;
}

.rodape p:last-of-type {
    width: 100%;
    border-top: 1px solid #8a8fa3;
}