.experiencias {
    padding: 5% 0;

}

.conteudo-experiencias *{

    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 2%;
}
.sun-experiencias {
    background-color: #f6f8fc;
    color: #8a8fa3;
}
.moon-experiencias {
    background-color: #393838;
    color: #cccccc;
}

.sun-experiencias h2::after, .moon-experiencias h2::after {
    content:'■■■■■■■■■■■■■■';
    color: #feb633;
    /* font-weight: 100; */

}

.container-cards {
    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.sun-titulo-experiencia{
    color: #32333c
}
.moon-titulo-experiencia{
    color: #fff
}


@media screen and (max-width: 768px) {
    .container-cards {
        flex-direction: column;
        padding: 0 5%;
    }
   
  
}