
header {
    min-height: 10vh;
}

.topo {

    min-width: 100%;
    
    position: fixed;
    top:0;
    right: 0;
    left: 0;
}

.topo div:first-of-type{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 1%;
}

.sun-topo {
    background-color: #fff;

}
.moon-topo {
    background-color: #252525;

}